const clockTitle = document.querySelector('.js-clock')

// clockTitle.innerText = "lala";

const CHRISTMAS_DATE = new Date('2023-12-25')
function getClock() {
  const today = new Date()
  const calcDate = CHRISTMAS_DATE - today

  const days = Math.floor(calcDate / (1000 * 60 * 60 * 24))
  const hours = Math.floor((calcDate / (1000 * 60 * 60)) % 24)
  const minutes = Math.floor((calcDate / (1000 * 60)) % 60)
  const seconds = Math.floor((calcDate / 1000) % 60)

  clockTitle.innerText = `${days}일 ${hours}시간 ${minutes}분 ${seconds}초`
}
setInterval(getClock, 1000)
